// import logo from './logo.svg';
import * as React from 'react';
import '../../App.css'
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Menu from '@mui/material/Menu';
import Container from '@mui/material/Container';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import MenuItem from '@mui/material/MenuItem';
import logo from '../../assets/logom.png'
import {  ListItemIcon, ListItemText } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import { loginPageUnLoggedUser} from '../../urls/apikeys';
import LogoutIcon from '@mui/icons-material/Logout';
import {ClipLoader} from "react-spinners"
import { globalColor } from '../../utils/globalColor';

function AppbarHomw() {
  const navigate = useNavigate()
  // menu Products 
  const [anchorEl, setAnchorEl] = React.useState(null);

  const [profile, setProfile] = React.useState(false)
  const [userEmail, setEmail] = React.useState('')
  // menu 
  const [anchorEl2, setAnchorEl2] = React.useState(null);
  const open2 = Boolean(anchorEl2);
  const handleClick2 = (event) => {
    setAnchorEl2(event.currentTarget);
  };
  const handleClose2 = () => {
    setAnchorEl2(null);
  };
  const Logout = () => {
    setLoaderLogout(true)
    setTimeout(() => {
      localStorage.removeItem('@UserSession')
      localStorage.removeItem('@PageRefreshed')
      setProfile(false)
      setAnchorEl(null);
    setLoaderLogout(false)

    }, 1000);

  }
  const [loaderLogout, setLoaderLogout] = React.useState(false)

  React.useEffect(() => {
    console.log('menu item')
    // get localstorage 

    const localMenuItem = localStorage.getItem('@UserSession')
    console.log(localMenuItem)
    if (localMenuItem === undefined || localMenuItem === null || localMenuItem === '') {
      setProfile(false)
    } else {
      setProfile(true)
      let emailData=JSON.parse(localMenuItem).email
      let emailPartBeforeAt = emailData.split('@')[0];
      setEmail(emailPartBeforeAt)
    }
  }, [])

  return (
    <>
      {/* Appbar  */}
      <AppBar position="fixed"  style={{
        height:'100px',
        paddingTop: '10px',
        top: 0,
        backgroundColor: 'white',
        // boxShadow: 'none',
        boxShadow: ' 0px 2px 30px -15px rgba(94,94,107,0.67)',
        borderBottom: '0.5px solid rgba(198,198,198,1);',
        
      }}>
        <Container maxWidth="xl" >
          <Toolbar disableGutters>
            <Avatar
            onClick={() => navigate('/')}

             sx={{ display: { xs: 'none', md: 'block'}, 
            cursor: 'pointer',
             width: "200px", height: 'auto' }} alt="Logo" src={logo} variant='square' />


            <Box sx={{ flexGrow: 1, display: { xs: 'flex', md: 'none' } }}>
             
              <Avatar sx={{  display: { xs: 'block', md: 'none' } , width: "100%", height: 'auto'}} alt="Logo" src={logo} variant='square'  />

            
            </Box>
            <Box sx={{ flexGrow: 1, display: { xs: 'none', md: 'flex' }, justifyContent: 'center' }}>
            </Box>
            <Box sx={{ display: 'flex', justifyContent: 'space-between' }}>
              {profile ? <>
                <Box
                  onClick={handleClick2}
                  style={{
                    display: 'flex',
                    alignItems: 'center',
                    cursor: 'pointer'
                  }}>
                  <Avatar alt={userEmail.charAt(0).toUpperCase() + userEmail.slice(1)}
                    src={userEmail.charAt(0).toUpperCase() + userEmail.slice(1)}
                    sx={{ width: 42, height: 42, backgroundColor: '#1890ff' }} />
                  <Typography className='poppinsSemiRegularBold' style={{ fontSize: '16px', color: 'black', marginLeft: '10px' }}>
                    {userEmail}
                  </Typography>

                </Box>
                {/* menu Logout  */}
                <Menu
                  PaperProps={{
                    elevation: 0,
                    sx: {
                      marginTop: '10px',
                      boxShadow: ' 0px 2px 30px -15px rgba(94,94,107,0.67)',
                    },
                  }}
                  id="basic-menu"
                  anchorEl={anchorEl2}
                  open={open2}
                  onClose={handleClose2}
                  MenuListProps={{
                    'aria-labelledby': 'basic-button',
                  }}
                >
                  <MenuItem
                    className='poppinsRegularBold'
                    style={{ backgroundColor: 'transparent' }} onClick={() => Logout()}>
                    <ListItemIcon>
                      {loaderLogout ?
                        <ClipLoader color="gray" loading={loaderLogout} size={20} /> :
                        <LogoutIcon fontSize="small" />}
                    </ListItemIcon>

                    <ListItemText>Logout</ListItemText>
                  </MenuItem>
                </Menu>
              </> :
                <>
             
                  <Button
                    onClick={() => {
                      window.open(`${loginPageUnLoggedUser}`)
                    }
                    }
                    className='poppinsRegularBold'
                    sx={{
                      width: '150px',
                      color: 'white',
                      bgcolor: "#03356b",
                      borderRadius: '50px',
                      fontSize: '16px',
                      transition: 'background-color 0.3s, color 0.3s', // Add transition properties
                      '&:hover': {
                        bgcolor: "white",
                        color: globalColor,
                        border: '1px solid #03356b', // Change text color to blue on hover
                        transitionDelay: '0.3s', // Add transition delay for hover
                      },
                      marginLeft: '10px',
                      textTransform: 'capitalize'

                    }}>Login</Button>
                </>}



            </Box>
          </Toolbar>
        </Container>
      </AppBar>



    </>
  );
}

export default AppbarHomw;
