import { Avatar, Grid, Button, Alert, Collapse, IconButton, TextField, Typography, CardContent, Card, Box } from '@mui/material';
import * as React from 'react';
import "../../App.css"
import './stylesheet.css'
import { post } from '../../urls/api';
import toastAlert from '../ToastAlert';
import { ClipLoader } from 'react-spinners';
import CloseIcon from '@mui/icons-material/Close';
import imageBg from "../../assets/aaa.png"
import { contactUsMessageAlert, contactUsMessageAlertError, contactusCardHead, contactusCardPara } from '../../urls/contactusKeys';
import { globalColor } from '../../utils/globalColor';
import { useTheme } from '@mui/material/styles';
function ContactCard() {
    const override = {
        display: "block",
        margin: "0 auto",
    };
    const [loader, setLoader] = React.useState(false)
    const [name, setName] = React.useState('')
    const [email, setEmail] = React.useState('')
    const [message, setMessage] = React.useState('')
    const [open, setOpen] = React.useState(false);
    const [msgAlert, setMessageAlert] = React.useState('success');
    const theme = useTheme();


    const SendMessage = async () => {
        setLoader(true)

        console.log("Sending Message")
        if (name === "" || email === "" || message === "") {
            // toastAlert("error", "Please fill all fields")
            setLoader(false)
        } else {
            // api call 
            try {
                const postData = {
                    name: name,
                    email: email,
                    message: message
                }
                const apiData = await post('contact_us/createContact', postData); // Specify the endpoint you want to call
                console.log(apiData)
                if (apiData.error) {
                    console.log("error true")
                    setLoader(false)
                    // toastAlert("error", "Something went wrong")
                    setMessageAlert('error')
                    setOpen(true)
                } else {
                    setLoader(false)
                    setMessageAlert('success')
                    setOpen(true)
                    // toastAlert("success", "Message Sent . We will contact you soon !")
                    setName('')
                    setEmail('')
                    setMessage('')
                }
            }
            catch (error) {
                setLoader(false)
            }
        }
    }
    return (
        <>

            <Grid container
                // id="smallScreenHero"
                style={{
                    display: 'flex',
                    alignItems: 'center',
                }}>
                <Grid item xs={12} md={1}>
                </Grid>
                <Grid item xs={12} md={5} style={{ position: 'relative' }}>


                    <Box
                        style={{
                            // boxShadow: ' 0px 2px 30px -15px rgba(94,94,107,0.67)',
                            padding: '4%'
                        }}>
                        <CardContent style={{ textAlign: 'left' }}>
                           
                            <div
                            // onClick={() => navigate(`/product_detail/${feature.product_id}`)}
                            >
                                <Typography className="poppinsSemiRegularBold"

                                    style={{
                                        color: '#03356b',
                                        fontSize: '50px',
                                        marginTop: '50px'
                                    }}
                                >
                                    Contact Us
                                </Typography>
                                <TextField label="Name" type="text"
                                    value={name}
                                    onChange={(e) => setName(e.target.value)}
                                    required
                                    style={{ width: "100%", marginBlock: '3%', borderRadius: '5px' }} />
                                <TextField label="Email" type="email"
                                    value={email}
                                    onChange={(e) => setEmail(e.target.value)}
                                    required
                                    style={{ width: "100%", marginBlock: '3%', borderRadius: '5px' }} />
                                <TextField label="Message" type="text"
                                    multiline
                                    rows={4}
                                    value={message}
                                    onChange={(e) => setMessage(e.target.value)}
                                    required
                                    style={{ width: "100%", marginBlock: '3%', borderRadius: '5px' }} />
                               <Collapse in={open}>
                                <Alert variant="filled"
                                    action={
                                        <IconButton
                                            aria-label="close"
                                            color="inherit"
                                            size="small"
                                            onClick={() => {
                                                setOpen(false);
                                            }}
                                        >
                                            <CloseIcon fontSize="inherit" />
                                        </IconButton>
                                    }
                                    sx={{ mb: 2 }}
                                >
                                    {msgAlert === "success" ? <>
                                        {contactUsMessageAlert}
                                    </> :
                                        <>
                                            {contactUsMessageAlertError}  </>}

                                </Alert>
                            </Collapse>
                                <Button
                                    onClick={() => SendMessage()}
                                    disabled={loader}
                                    startIcon={loader ?
                                        <ClipLoader
                                            color="Gray"
                                            loading={loader}
                                            cssOverride={override}
                                            size={10}
                                            aria-label="Loading Spinner"
                                            data-testid="loader"
                                        /> : null}
                                    className='poppinsRegularBold'
                                    sx={{
                                        width: '30%',
                                        [theme.breakpoints.down('sm')]: {
                                            width: '100%', // Change the width to 100% on small screens
                                        },
                                        marginTop: '2%',
                                        color: 'white',
                                        bgcolor: "#03356b",
                                        borderRadius: '50px',
                                        //center button
                                        fontSize: '16px',
                                        transition: 'background-color 0.3s, color 0.3s', // Add transition properties
                                        '&:hover': {
                                            bgcolor: "white",
                                            color: globalColor,
                                            border: '1px solid #03356b', // Change text color to blue on hover
                                            transitionDelay: '0.3s', // Add transition delay for hover
                                        },
                                        // disable button css 
                                        '&.Mui-disabled': {
                                            color: 'white',
                                            backgroundColor: globalColor,
                                            opacity: 0.6,
                                            cursor: 'not-allowed',
                                            border: 'none',
                                        },
                                        marginLeft: '10px',
                                        textTransform: 'capitalize'

                                    }}>Submit</Button>

                            </div>

                        </CardContent>

                    </Box>
                    {/* </Card> */}


                </Grid>
                <Grid item xs={12} md={1} id="smallScreenImageLarge">
                </Grid>
                <Grid item xs={12} md={5} >
                    <Avatar id="smallScreenImageLarge"
                        variant='square'
                        src={imageBg} alt="Circle" style={{
                            width: '100%', height: 'auto',
                            marginBottom: 0,
                            paddingBottom: 0,
                            display: { xs: 'none', md: 'block' }
                        }} />
                </Grid>
            </Grid>
        </>
    );
}

export default ContactCard;