// import logo from './logo.svg';
import * as React from 'react';
import '../App.css';
import { Grid, Typography } from '@mui/material'
import Avatar from '@mui/material/Avatar';
import InternetError from "../assets/payment_success.png"
// import ClipLoader from "react-spinners/ClipLoader";
import Button from '@mui/material/Button';
import { useNavigate } from 'react-router-dom';
import { useEffect } from 'react';
import { post } from '../urls/api';
import toast from 'react-hot-toast';
import { globalColor } from '../utils/globalColor';

function PaymentSuccess() {
  const color = "rgb(150, 143, 143)"
  const override = {
    display: ' block',
    margin: '0 auto',
  }
  const [disabled, setdisabled] = React.useState(false)
  const navigate = useNavigate()
 
  useEffect(() => {
 const createAccount = async () => {

    //   checkEmailUser 
    try {
      const userSession = JSON.parse(
        localStorage.getItem("@UserEmailPayment")
      );
      console.log(userSession)
      const postData = {
        email: userSession.email,
        program_id: userSession.program_id,
        package_id: userSession.package_id,
      }
      const apiData = await post(`user/checkEmailUser`, postData); // Specify the endpoint you want to call
      console.log("apiData")

      console.log(apiData)
      if (apiData.error) {
        console.log("error true")
        //   toast.error(apiData.error)
        // setLoader(false)

      } else {
        console.log("PRICE")
        localStorage.setItem("@UserSession", JSON.stringify({ email: userSession.email }))

        // setVideodata(apiData.data.videos)
        // setFirstVideo(apiData.data.videos[0].thumbnail)
        // setFirstVideotitle(apiData.data.videos[0].title)

      }
    } catch (error) {
      // setLoader(false)

    }
  }
    createAccount()
  }, []);


  return (
    <>
      < Grid container spacing={2}>
        <Grid item xs={12} md={3} align="center">
        </Grid>
        <Grid item xs={12} md={6} align="center" style={{ height: '90vh', display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center' }}>
          <Avatar src={InternetError} variant="square" style={{ width: '300px', height: '300px' }} ></Avatar>
          <Typography variant="h3" fontWeight={700} style={{ marginBlock: '1%' }} className='poppinsRegularBold'>
            Your Payment is Successful
          </Typography>
          <Typography variant="h6" fontWeight={500} style={{ marginBlock: '2%' }} className='poppinsRegularBold'>
            Thank you for your payment .An automated information for user login is sent to your email
          </Typography>

          <Button onClick={() => navigate('/')}
            className='poppinsRegularBold'
            sx={{
              width: '30%',
              marginBlock: '4%',
              color: 'white',
              bgcolor: "#03356b",
              borderRadius: '50px',
              fontSize: '16px',
              transition: 'background-color 0.3s, color 0.3s', // Add transition properties
              '&:hover': {
                bgcolor: "white",
                color: globalColor,
                border: '1px solid #03356b', // Change text color to blue on hover
                transitionDelay: '0.3s', // Add transition delay for hover
              },
              marginLeft: '10px',
              textTransform: 'capitalize'

            }}>Back to Home</Button>
        </Grid>
        <Grid item xs={12} md={3} align="center">
        </Grid>

      </Grid>

    </>
  );
}

export default PaymentSuccess;
