import { Grid, Typography, Skeleton, Tooltip, Button } from '@mui/material';
import * as React from 'react';
import "../../App.css"
import './stylesheet.css'
import { useNavigate } from 'react-router-dom';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';

function HeroProduct({ name }) {
    const navigate = useNavigate();
    return (
        <>

            <Grid container spacing={2}
                // id="smallScreenHero"
                style={{
                    marginTop: 120,
                    backgroundColor: 'white',

                    display: 'flex',
                    alignItems: 'center',


                }}>
                <Grid item xs={12} md={1}>
                </Grid>


                <Grid item xs={12} md={11} >
                    <Grid container>
                        <Grid item xs={12} md={12} style={{ display: 'flex' }}>
                            <Tooltip title="Back">


                                <Button
                                    onClick={() => {
                                        navigate(`/`)
                                    }
                                    }
                                    className='poppinsRegularBold'
                                    sx={{
                                        // width: '100px',
                                        paddingInline: '10px',
                                        display: "block",
                                        color: "#03356b",
                                        backgroundColor: "none",
                                        fontSize: '16px',
                                        fontWeight: 600,
                                        borderRadius: '50px',
                                        textTransform: 'capitalize',
                                        transition: 'background-color 0.3s, color 0.3s', // Add transition properties
                                        transitionDelay: '0.3s', // Add transition delay for hover
                                        marginRight: '10px',
                                        // border: '1px solid #03356b',
                                        '&:hover': {
                                            backgroundColor: "#03356b",
                                            color: 'white', // Change text color to blue on hover
                                        },
                                    }} >
                                    <ArrowBackIcon />
                                </Button>
                            </Tooltip>
                            <Typography className='poppinsSemiRegularBold' variant='h3' style={{ color: '#03356b', marginBottom: '10px', width: '100%' }}>
                                {name === "undefined" || name === null || name === "" ? <
                                    >
                                    <Skeleton variant="rectangular" height={50} width={300} />
                                </> : name}
                            </Typography>
                        </Grid>

                    </Grid>


                </Grid>


                <Grid item xs={12} md={1} id="smallScreenImageLarge">
                </Grid>


            </Grid>
        </>
    );
}

export default HeroProduct;