import { Avatar, Box, Button, Card, CardActions, CardContent, CardHeader, CardMedia, Divider, Grid, IconButton, Skeleton, Stack, Typography } from '@mui/material';
import * as React from 'react';
import "../../App.css"
import ContainedButton from '../Buttons/ContainedButton';
import ScrollAnimation from 'react-animate-on-scroll';
import StarIcon from '@mui/icons-material/Star';
import { BASE_URL, get } from '../../urls/api';
import { useNavigate } from 'react-router-dom';
import { productsHead, productsHead2, productsParaPart1, productsParaPart2 } from '../../urls/contactusKeys';
import PersonIcon from '@mui/icons-material/Person';
import OndemandVideoIcon from '@mui/icons-material/OndemandVideo';
import ArrowForwardIcon from '@mui/icons-material/ArrowForward';
import { globalColor } from '../../utils/globalColor';
import { useTheme } from '@mui/material/styles';
function Features2({ productALL }) {
    const navigate = useNavigate()
    const [loader, setLoader] = React.useState(true)
    const [products, setProducts] = React.useState([])
    const theme = useTheme();
    const fetchProducts = async () => {

        try {
            const apiData = await get('package/getAllPackages'); // Specify the endpoint you want to call
            console.log(apiData)
            if (apiData.error) {
                console.log("error true")
                setLoader(false)

            } else {
                console.log(apiData.result)

                console.log("COURSSES")
                let arrayOfObjects = apiData.result
                let firstThreeObjects = arrayOfObjects.slice(0, 3);
                setProducts(firstThreeObjects)
                setLoader(false)

            }
        } catch (error) {
            setLoader(false)

        }

    }

    React.useEffect(() => {
        fetchProducts();
    }, []);
    return (
        <>
            {loader ? <>
                <Grid container spacing={2} pt={5} style={{ backgroundColor: "rgb(250 250 250)" }}>
                    {/* For variant="text", adjust the height via font-size */}
                    <Grid item xs={12} md={1}>
                    </Grid>
                    <Grid item xs={12} md={10} >
                        <Grid container spacing={2}>
                            <Grid item xs={12} md={12} align="center">
                                {/* <Typography className='poppinsSemiRegularBold' variant='h4' style={{ color: '#041d34' }}>
                            Our Product
                            </Typography> */}
                                <Typography id="productHeader" className='poppinsSemiRegularBold' variant='h2' style={{ color: '#03356b', marginBottom: '10px', width: '100%' }}>
                                    {productsHead2}
                                </Typography>

                            </Grid>
                            <Grid item xs={12} md={4}>
                                <Skeleton variant="rectangular" height={300} />

                            </Grid>
                            <Grid item xs={12} md={4}>
                                <Skeleton variant="rectangular" height={300} />

                            </Grid>
                            <Grid item xs={12} md={4}>
                                <Skeleton variant="rectangular" height={300} />

                            </Grid>
                        </Grid>
                        {/* For other variants, adjust the size with `width` and `height` */}
                        {/* <Skeleton variant="rectangular" />
                        <Skeleton variant="rounded" width={210} height={60} /> */}
                    </Grid>
                    <Grid item xs={12} md={1}>
                    </Grid>
                </Grid>
            </> :
                <>
                    <Grid container spacing={2} style={{ backgroundColor: "rgb(250 250 250)", paddingBlock: '2%' }}>
                        <Grid item xs={12} md={1}>
                        </Grid>
                        <Grid item xs={12} md={10} >
                            <Grid container>
                                <Grid item xs={12} md={12} align="center" style={{ display: 'flex', justifyContent: 'center' }}>
                                    {/* <Typography className='poppinsSemiRegularBold' variant='h4' style={{ color: '#041d34' }}>
                            Our Product
                            </Typography> */}
                                    <Typography id="productHeader" className='poppinsSemiRegularBold' variant='h2' style={{ color: '#03356b', marginBottom: '10px', width: '100%' }}>
                                        {productsHead2}
                                    </Typography>


                                </Grid>

                                <Grid item xs={12} md={12} align="center" style={{ marginTop: '5%' }}>
                                    <Grid container spacing={1}>
                                        {products.map((feature) => (
                                            <>

                                                <Grid item xs={12} md={4} mt={2} align="center">
                                                    <ScrollAnimation animateIn="fadeIn">


                                                        {/* Card  */}
                                                        <Card

                                                            sx={{
                                                                maxWidth: 375,
                                                                boxShadow: '0px 6px 34px rgba(215, 216, 222, 0.41);',
                                                                padding: '30px',
                                                                // boxShadow: ' 0px 2px 30px -15px rgba(94,94,107,0.67)',
                                                                borderRadius: '6px',
                                                                cursor: 'pointer',
                                                                transition: 'transform 0.3s', // Add transition for zoom effect
                                                                '&:hover': {
                                                                    boxShadow: ' 0px 2px 30px -15px rgba(94,94,107,0.67)',

                                                                    transform: 'scale(1.02)' // Zoom effect on hover
                                                                }
                                                            }}>

                                                            <Box
                                                                onClick={() => navigate(`/product_detail/${feature.package_id}`)}
                                                            >
                                                                <CardMedia
                                                                    component="img"
                                                                    style={{
                                                                        border: '1px solid lightGray',
                                                                        borderRadius: '6px'
                                                                    }}
                                                                    height="214"
                                                                    image={`${BASE_URL}${feature.image}`}
                                                                    alt="Paella dish"
                                                                />
                                                            </Box>
                                                            <CardContent style={{ textAlign: 'left' }}>
                                                                <div
                                                                    onClick={() => navigate(`/product_detail/${feature.package_id}`)}
                                                                >
                                                                    <Typography
                                                                        style={{
                                                                            fontSize: '20px',
                                                                            color: globalColor,
                                                                            whiteSpace: 'nowrap',
                                                                            overflow: 'hidden',
                                                                            textOverflow: 'ellipsis',
                                                                            maxWidth: '25ch',
                                                                            fontWeight: 700,
                                                                        }}
                                                                        title={feature.package_name.length > 25 ? feature.package_name : null}
                                                                    >
                                                                        {feature.package_name.length > 25
                                                                            ? `${feature.package_name.slice(0, 25)}...`
                                                                            : feature.package_name}
                                                                    </Typography>
                                                                    <Box style={{ display: 'flex', justifyContent: 'space-between' }}>
                                                                        <Box style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
                                                                            <OndemandVideoIcon style={{ fontSize: '16px', color: '#6b7385', marginTop: '8px' }} />
                                                                            <Typography
                                                                                className="poppinsRegularBold"
                                                                                style={{
                                                                                    fontSize: '14px',
                                                                                    marginTop: '10px',
                                                                                    whiteSpace: 'wrap',
                                                                                    overflow: 'hidden',
                                                                                    textOverflow: 'ellipsis',
                                                                                    color: '#6b7385;',
                                                                                    marginLeft: '5px'

                                                                                }}

                                                                            >{feature.videos} Lectures
                                                                            </Typography>
                                                                        </Box>
                                                                        <Box style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
                                                                            <PersonIcon style={{ fontSize: '16px', color: '#6b7385', marginTop: '8px' }} />
                                                                            <Typography
                                                                                className="poppinsRegularBold"
                                                                                style={{
                                                                                    fontSize: '14px',
                                                                                    marginTop: '10px',
                                                                                    whiteSpace: 'wrap',
                                                                                    overflow: 'hidden',
                                                                                    textOverflow: 'ellipsis',
                                                                                    color: '#6b7385;',
                                                                                    marginLeft: '5px'

                                                                                }}

                                                                            >{feature.enrolled_students}  Students
                                                                            </Typography>
                                                                        </Box>


                                                                    </Box>
                                                                    <Typography
                                                                        className="poppinsRegularBold"
                                                                        style={{
                                                                            fontSize: '18px',
                                                                            marginTop: '10px',
                                                                            whiteSpace: 'wrap',
                                                                            overflow: 'hidden',
                                                                            textOverflow: 'ellipsis',
                                                                            maxWidth: '60ch',
                                                                        }}
                                                                        title={feature.description.length > 90 ? feature.description : null}
                                                                    >
                                                                        {/* {feature.description} */}
                                                                        {feature.description.length > 90
                                                                            ? `${feature.description.slice(0, 90)}...`
                                                                            : feature.description}

                                                                    </Typography>
                                                                    <div style={{display:'flex'}}>

                                                                  
                                                                    <Typography
                                                                        className="poppinsRegularBold"
                                                                        style={{
                                                                            fontSize: '24px',
                                                                            marginTop: '10px',
                                                                            whiteSpace: 'wrap',
                                                                            overflow: 'hidden',
                                                                            textOverflow: 'ellipsis',
                                                                            color: '#6b7385;',
                                                                            fontWeight: 900,
                                                                            textDecoration: 'line-through',

                                                                        }}

                                                                    >{feature.package_price} PKR
                                                                    </Typography>
                                                                    <Typography
                                                                        className="poppinsRegularBold"
                                                                        style={{
                                                                            marginLeft:'10px',
                                                                            fontSize: '24px',
                                                                            marginTop: '10px',
                                                                            whiteSpace: 'wrap',
                                                                            overflow: 'hidden',
                                                                            textOverflow: 'ellipsis',
                                                                            color: '#6b7385;',
                                                                            fontWeight: 900

                                                                        }}

                                                                    > {feature.package_offer_price} PKR
                                                                    </Typography>
                                                                    </div>
                                                                    {/* <span style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', marginTop: '10px' }}>

                                                                        <div style={{ display: 'flex' }}>
                                                                            <Typography className="poppinsRegularBold" style={{ fontSize: '15px', fontWeight: 600, }}>
                                                                                Start at : $ {feature.price}

                                                                            </Typography>
                                                                        </div>
                                                                        <div style={{ display: 'flex' }}>

                                                                            <StarIcon style={{ color: 'orange' }} />
                                                                            <Typography className="poppinsRegularBold" style={{ fontSize: '15px' }}>
                                                                                (5)

                                                                            </Typography>
                                                                        </div>
                                                                    </span> */}
                                                                    {/* <Divider style={{ marginTop: '10px' }} /> */}

                                                                </div>
                                                                <Box style={{ display: 'flex', justifyContent: 'center', marginTop: '10px' }}>

                                                                    <Button
                                                                        onClick={() => navigate(`/product_detail/${feature.package_id}/#pricing`)}
                                                                        className='poppinsRegularBold'
                                                                        sx={{
                                                                            width: '40%',
                                                                            [theme.breakpoints.down('sm')]: {
                                                                                width: '100%', // Change the width to 100% on small screens
                                                                            },
                                                                            backgroundColor: "#03356b",
                                                                            paddingInline: '20px',
                                                                            color: 'white',
                                                                            fontSize: '16px',
                                                                            fontWeight: 600,
                                                                            borderRadius: '50px',

                                                                            textTransform: 'capitalize',
                                                                            transition: 'background-color 0.3s, color 0.3s', // Add transition properties
                                                                            transitionDelay: '0.3s', // Add transition delay for hover
                                                                            border: '1px solid white',

                                                                            '&:hover': {
                                                                                // backgroundColor: "#03356b",,
                                                                                border: '1px solid #03356b',
                                                                                color: globalColor, // Change text color to blue on hover
                                                                            },

                                                                        }} >Buy now</Button>


                                                                </Box>
                                                            </CardContent>
                                                            {/* <CardActions disableSpacing>
                                                        <IconButton aria-label="add to favorites">
                                                            <FavoriteIcon />
                                                        </IconButton>
                                                        <IconButton aria-label="share">
                                                            <ShareIcon />
                                                        </IconButton>
                                                      
                                                    </CardActions> */}
                                                        </Card>
                                                        {/* End  */}
                                                    </ScrollAnimation>

                                                </Grid>
                                            </>))}
                                    </Grid>

                                </Grid>
                            </Grid>


                        </Grid>
                        <Grid item xs={12} md={1}>
                        </Grid>



                    </Grid>
                </>}

        </>
    );
}

export default Features2;