// import logo from './logo.svg';
import * as React from 'react';
import '../App.css';
import AppbarHomw from '../components/Appbar';
import ProductHero from '../components/ProductHero';
import Products from '../components/Products';
import PricingSection from '../components/PricingPage';
import { useParams } from 'react-router-dom';
import { get, post } from '../urls/api';
import Footer from '../components/Footer';
import HeroLearn from '../components/HeroLearn';
import HeroProduct from '../components/HeroProduct';

function ProductDetails() {
  const firstSectionRef = React.useRef(null);


  const pricingSectionRef = React.useRef(null);

  // Function to scroll to the pricing section
  const scrollToPricing = () => {
    if (pricingSectionRef.current) {
      pricingSectionRef.current.scrollIntoView({ behavior: 'smooth' });
    }
  };
  const { id } = useParams()
  const [name, setName] = React.useState('')
  const [description, setDescription] = React.useState('')
  const [image, setImage] = React.useState('')
  const [features, setFeatures] = React.useState('')
  const [pricing, setPricing] = React.useState('')
  const [StripePricingId, setStripePricingId] = React.useState('')
const [videodata,setVideodata]=React.useState([])
  const fetchProduct = async () => {
    try {
      const postData = {
        package_id: id
      }
      const apiData = await post(`package/getByProgramId`,postData); // Specify the endpoint you want to call
      console.log("apiDataddd")

      console.log(apiData)
      if (apiData.error) {
        console.log("error true")
        // setLoader(false)

      } else {
        console.log(apiData.data.course)
        setName(apiData?.data?.course?.package_name)
        setDescription(apiData?.data?.course?.description)
        // setStripePricingId(apiData?.data?.product?.product_id_stripe)
        setImage(apiData?.data?.course?.image)
        // setFeatures(apiData?.data?.product?.features)
        // setPricing(apiData?.data?.prices)
        // setVideodata(apiData?.data?.videos)
        const hash = window.location.hash;

        
          if (hash === '#pricing') {
            const pricingSection = document.getElementById('pricing');
            if (pricingSection) {
              pricingSection.scrollIntoView({ behavior: 'smooth' });
            }
          }
                
      }
    } catch (error) {
      // setLoader(false)

    }
  }


  React.useEffect(() => {
    // When the component mounts, scroll to the first section
    if (firstSectionRef.current) {
      firstSectionRef.current.scrollIntoView({
        behavior: 'smooth',
      });

    }

    fetchProduct()
  }, []); // The empty dependency array ensures this effect runs only once when the component mounts



  return (
    <>

      {/* Appbar  */}
      <div style={{ backgroundColor: 'white' }} ref={firstSectionRef}>
        <AppbarHomw  name={name}/>
        {/* Appbar  end */}
        <div >

          {/* <ProductHero name={name} description={description} image={image} 
          id={id}
          onSubscribeClick={scrollToPricing}
          /> */}
        </div>
        <HeroProduct name={name} description={description} image={image}/>

        <HeroLearn  id={id} videodata={videodata}/>
        {/* <Products features={features} /> */}
        {/* <div 
         ref={pricingSectionRef}
        id="pricing">
          <PricingSection
           id={id}
          //  pricing={pricing}
            // pricingId={StripePricingId}
          />
        </div> */}
        <Footer />
      </div>

    </>
  );
}

export default ProductDetails;
