import { Avatar, Grid, Typography } from '@mui/material';
import * as React from 'react';
import "../../App.css"
import circle from "../../assets/homeimage2.png"
import './stylesheet.css'
import { heroParagraph, heroheaderPart1, heroheaderPart2 } from '../../urls/contactusKeys';
import Marquee from "react-fast-marquee";
import { get } from '../../urls/api';
import { useState } from 'react';
import { Link } from 'react-router-dom';
import FiberManualRecordIcon from '@mui/icons-material/FiberManualRecord';

function HeroSection() {
    const [blogs,setBlogs]=useState([])
    const getLatestNews=async()=>{
        console.log("get all latest news")
       
        const apiData = await get('blogs/getAllBlogsActive'); // Specify the endpoint you want to call
        console.log("Active Blogs Details")
        console.log(apiData)
        if (apiData.error) {
        } else {
            setBlogs(apiData.result)
          }
    }
   React.useEffect(() => {
     getLatestNews()
   }, [])

    return (
        <>

            <Grid container spacing={2}
                id="smallScreenHero"
                style={{
                    // backgroundColor:'#734836',
                    background: 'rgb(255,255,255)',
                    // background:' linear-gradient(73deg, rgba(255,255,255,1) 16%, rgba(3,53,107,1) 100%)',
                    background: 'linear-gradient(126deg, rgba(255,255,255,1) 34%, rgba(189,243,254,0.3029586834733894) 100%)',
                    display: 'flex',
                    alignItems: 'center',
                    marginTop: 50


                }}>
                    <Grid item xs={12} md={12} style={{backgroundColor:'red',}}>
                    <Marquee>
                    <Typography className='poppinsSemiRegularBold'

variant='h6' style={{  color: 'white', marginTop: '47px', width: '100%', textTransform: 'capitalize' }}>
 <span className='poppinsLight' style={{fontWeight:400,display:'flex',justifyContent:'center',alignItems:'center'}}>
    {blogs.map((item)=>(<>
        <FiberManualRecordIcon style={{marginInline:'20px',fontSize:'14px'}}/>
<Link style={{color:'white',textDecoration:'none'}} to={`/blog/${item.blog_id}`}> {item.title}<tab/>
</Link> {' '} {/* Add a space between links */}

</>))}
</span>

</Typography>
                        </Marquee>
                    </Grid>
                <Grid item xs={12} id="smallScreenImage" md={6} style={{ position: 'relative',marginTop:'20px' }}>
                    <Avatar
                        variant='square'
                        src={circle} alt="Circle" style={{
                            marginTop: '40px',
                            width: '100%', height: 'auto',
                            display: { xs: 'none', md: 'block' }
                        }} />
                        {/* make circle to put on back of image  */}

                </Grid>
               
                <Grid item xs={12} md={1} id="smallScreenImageLarge">
                </Grid>
                <Grid item xs={12} md={5} >
                    <Grid container>
                        <Grid item xs={12} md={12}>
                            <Typography className='poppinsSemiRegularBold'

                                variant='h5' style={{ color: 'black', marginBottom: '10px', width: '100%', textTransform: 'capitalize' }}>
                                WELCOME TO TSM ACADEMY
                            </Typography>
                            <Typography className='poppinsSemiRegularBold' id="header"
                                sx={{
                                    fontSize: {
                                        md: '4vw!important',
                                    }

                                }}
                                variant='h3' style={{ color: '#03356b', marginBottom: '10px', width: '100%' }}>
                                {heroheaderPart1}<br />{heroheaderPart2}
                            </Typography>
                            <Typography variant='h6' id="subheader" className='poppinsLight' style={{ color: '#888888', fontweight: 700 }}>
                                {heroParagraph}
                            </Typography>
                        </Grid>
                        {/* <Grid item xs={12} md={12} mt={4}>
                            <ContainedButton text="Explore Products" onClick={()=>navigate('/products')} />

                        </Grid> */}

                    </Grid>


                </Grid>
                <Grid item xs={12} md={5} mt={-5} id="smallScreenImageLarge" style={{ position: 'relative' }}>
                    <Avatar src={circle} alt="Circle" variant='square' style={{
                        width: '100%', height: 'auto',
                        marginTop: '60px',

                        display: { xs: 'none', md: 'block' }
                    }} />


                </Grid>
                <Grid item xs={12} md={1}>
                </Grid>
            </Grid>
        </>
    );
}

export default HeroSection;