import { Avatar, Grid, Typography } from '@mui/material';
import React, {
    useState
} from 'react';
import "../../App.css"
import Slider from "react-slick";
import imageAvatar from "../../assets/avatar.png"
import { BASE_URL, get } from '../../urls/api';
import Rating from '@mui/material/Rating';

// import { sliderSArray } from '../../urls/contactusKeys';

function SliderSection() {
    const [currentSlide, setCurrentSlide] = useState(0);
    const [sliderSArray, setSliderSArray] = useState([])
    const [loader, setLoader] = useState(true)
    const settings = {
        dots: true,
        // infinite: true,
        // speed: 500,
        // slidesToShow: 1,
        // slidesToScroll: 1
        className: "center",
        centerMode: true,
        infinite: true,
        centerPadding: "60px",
        slidesToShow: 1,
        // autoplay: true,
        speed: 500,

        // autoplaySpeed: 500,

    };

    const settings1 = {
        ...settings,
        beforeChange: (oldIndex, newIndex) => {
            setCurrentSlide(newIndex);
        }
    };
    const fetchProducts = async () => {

        try {
            const apiData = await get('review/getAllReviews'); // Specify the endpoint you want to call
            console.log(apiData)
            if (apiData.error) {
                console.log("error true")
                // setLoader(false)

            } else {
                console.log(apiData.result)

                console.log("Reviews")

                setSliderSArray(apiData.result)
                // setLoader(false)

            }
        } catch (error) {
            setLoader(false)

        }

    }

    React.useEffect(() => {
        fetchProducts();
    }, []);
    return (
        <>

            <Grid container spacing={2} style={{
                backgroundColor: "transparent",
                marginBlock: '10%',
            }} >
                <Grid item xs={12} md={1}>
                </Grid>
                <Grid item xs={12} md={10} >
                    <div

                    >
                        <Slider
                            {...settings1}
                        >
                            {sliderSArray.map((item, index) => (
                                <div
                                    key={index}
                                    style={{
                                        opacity: currentSlide === index ? 1 : 0, // Change opacity based on the active slide
                                        transition: 'opacity 0.5s'
                                    }}
                                >
                                    {/* <h3>1</h3> */}
                                    <Grid container spacing={2}>
                                        <Grid item xs={12} md={2} align="center">
                                        </Grid>
                                        <Grid item xs={12} md={8} align="center" >
                                            <Typography variant='h4'
                                                sx={{
                                                    fontFamily: 'Poppins',
                                                    // fontWeight: 700,
                                                    fontSize: '25px',
                                                    lineHeight: '36px',
                                                    color: '#041d34',
                                                    marginBlock: '2%'

                                                }} className='poppinsRegularBold' >
                                                {item.review}
                                            </Typography>
                                            <Avatar src={`${BASE_URL}${item.image}`} alt="imageAvatar" style={{ marginTop: '40px' }} />
                                            <Typography variant='h6' id="sliderComenter" className='poppinsSemiRegularBold' >
                                                {item.name}
                                            </Typography>
                                            {/* <Typography variant='h6' id="sliderComenter" className='poppinsLight'  >
                                                {item.rating}
                                            </Typography> */}
                                            <Rating name="size-large" value={item.rating}
                                                // onChange={(event, newValue) => {
                                                //   setValue(newValue);
                                                // }} 
                                                size="large" />
                                        </Grid>
                                        <Grid item xs={12} md={2} align="center">
                                        </Grid>
                                    </Grid>
                                </div>
                            ))}
                        </Slider>
                    </div>


                </Grid>
                <Grid item xs={12} md={1} >


                </Grid>
            </Grid>
        </>
    );
}

export default SliderSection;