// import logo from './logo.svg';
import * as React from 'react';
import '../App.css';
import { Grid, Skeleton, Typography } from '@mui/material'
import Avatar from '@mui/material/Avatar';
import InternetError from "../assets/errorPage.png"
// import ClipLoader from "react-spinners/ClipLoader";
import Button from '@mui/material/Button';
import { useNavigate, useParams } from 'react-router-dom';
import { globalColor } from '../utils/globalColor';
import { BASE_URL, post } from '../urls/api';
import { useState } from 'react';
import { useEffect } from 'react';
import AppbarHomw from '../components/Appbar';
import Footer from '../components/Footer';

function Blog() {
    const firstSectionRef = React.useRef(null);

    const color = "rgb(150, 143, 143)"
    const override = {
        display: ' block',
        margin: '0 auto',
    }
    const [disabled, setdisabled] = useState(false)
    const navigate = useNavigate()

    const [blogTitle, setBlogTitle] = useState('')
    const [blogDescription, setBlogDescription] = useState('')
    const [blogImage, setBlogImage] = useState('')

    const { blog_id } = useParams()
    const getLatestNews = async () => {
        console.log("get all latest news")
        const postData = {
            blog_id: blog_id
        }
        const apiData = await post('blogs/getBlogDetails', postData); // Specify the endpoint you want to call
        console.log("Active Blogs Details")
        console.log(apiData.data)
        if (apiData.error) {
        } else {
            setBlogTitle(apiData?.data?.title)
            setBlogDescription(apiData?.data?.description)
            setBlogImage(apiData?.data?.image)
            // setBlogs(apiData.result)
        }
    }
    useEffect(() => {
        getLatestNews()

        if (firstSectionRef.current) {
            firstSectionRef.current.scrollIntoView({
                behavior: 'smooth',

            });
        }
    }, [])
    return (
        <>
            <div style={{ backgroundColor: 'white', overflowX: 'hidden ' }} ref={firstSectionRef}>

                <AppbarHomw />
            </div>
            < Grid container spacing={2} mt={15}>
                <Grid item xs={12} md={3} align="center">
                </Grid>
                <Grid item xs={12} md={6} align="center" style={{ height: 'auto', display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center' }}>
                    <Typography variant="h4" fontWeight={700} style={{ marginBlock: '1%', textAlign: 'left', lineHeight: '2' }} className='poppinsRegularBold'>
                        {blogTitle === null || blogTitle === undefined || blogTitle === '' ? <>
                            <Skeleton variant="rectangular" height={100} width="100%" style={{marginBlock:'1%'}} />

                        </> :
                            <> {blogTitle}</>}

                    </Typography>
                    {blogImage === null || blogImage === undefined || blogImage === '' ? <>
                        <Skeleton variant="rectangular" height={100} width="100%" />

                    </> :
                        <Avatar src={`${BASE_URL}${blogImage}`} variant="square" style={{ width: '100%', height: 'auto' }} ></Avatar>
                    }
                    <Typography variant="h6" fontWeight={500} style={{ marginBlock: '2%', textAlign: 'justify' }} className='poppinsRegularBold'>
                        {blogDescription === null || blogDescription === undefined || blogDescription === '' ? <>

                            <Skeleton variant="rectangular" height={100} width="100%" />
                        </> :
                            <div dangerouslySetInnerHTML={{ __html: blogDescription }} />}
                    </Typography>


                </Grid>
                <Grid item xs={12} md={3} align="center">
                </Grid>

            </Grid>
            <Footer />

        </>
    );
}

export default Blog;
