import { Avatar, Box, Button, Card, CardActions, CardContent, CardHeader, CardMedia, Divider, Grid, IconButton, Skeleton, Stack, Typography } from '@mui/material';
import * as React from 'react';
import "../../App.css"
import { get } from '../../urls/api';
import { useNavigate } from 'react-router-dom';
import { productsHead3, productsHead4 } from '../../urls/contactusKeys';

function ProductsData() {
    const navigate = useNavigate()
    const [loader, setLoader] = React.useState(true)
    const [firstVideoUrl, setFirstVideoUrl] = React.useState(null)
    const fetchProducts = async () => {

        try {
            const apiData = await get('program/get'); // Specify the endpoint you want to call
            console.log(apiData)
            if (apiData.error) {
                console.log("error true")
                setLoader(false)

            } else {
                console.log(apiData.result)
                const data = apiData.result[0].video_url
                let newHeight;
                if (window.innerWidth <= 600) { // 600px is generally the breakpoint for mobile devices
                    newHeight = '100%';
                } else {
                    newHeight = '700px';
                } // Set your desired height here
                const borderRadius = '20px'; // Set your desired border radius here

                let updatedVideoUrl = data.replace('height="100%"', `height="${newHeight}"`);
                // updatedVideoUrl = updatedVideoUrl.replace('allowfullscreen=""', `allowfullscreen="" style="border-radius: ${borderRadius};"`);
                // let firstThreeObjects = arrayOfObjects.slice(0,3);
                setFirstVideoUrl(updatedVideoUrl)
                setLoader(false)

            }
        } catch (error) {
            setLoader(false)

        }

    }

    React.useEffect(() => {
        fetchProducts();
    }, []);
    return (
        <>

            <Grid container spacing={2} pt={10} style={{ backgroundColor: "transparent", marginBlock: '2%', marginBottom: '6%' }}>
                
                <Grid item xs={12} md={2}>
                </Grid>
                <Grid item xs={12} md={8} >
                    <Grid container>
                    <Grid item xs={12} md={12} align="center">
                <Typography id="productHeader" className='poppinsSemiRegularBold' variant='h2' style={{ color: '#03356b', marginBottom: '10px', width: '100%' }}>
                                        {productsHead3}
                                    </Typography>
                                    <Typography className='poppinsLight' variant='h5' style={{ fongtSize:'14px', marginBottom: '10px', width: '100%' }}>
                                        {productsHead4}
                                    </Typography>
                </Grid>

                        <Grid item xs={12} md={12} align="center" style={{ marginBlock: '1%', height: '100%', width: '100%' }}>
                            {/* Video  */}
                            {firstVideoUrl === null || firstVideoUrl === undefined || firstVideoUrl === "" ? <>
                                <Skeleton variant="rectangular" height={500} width="100%" />
                            </> : <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', width: '100%', borderRadius: '10px' }}
                                dangerouslySetInnerHTML={{
                                    __html: ` ${firstVideoUrl}`
                                }}
                            />}

                        </Grid>



                    </Grid>


                </Grid>
                <Grid item xs={12} md={2}>
                </Grid>



            </Grid>


        </>
    );
}

export default ProductsData;