import { Avatar, Box, Grid, Typography } from '@mui/material';
import * as React from 'react';
import "../../App.css"
import ContainedButton from '../Buttons/ContainedButton';
import imageDummy from "../../assets/imageDummy.jpg"
import imageDummy1 from "../../assets/imageDummy2.jpg"
import imageDummy2 from "../../assets/imageDummy3.jpg"
import arrow from "../../assets/arrow.png"
import arrow1 from "../../assets/arrow1.png"
import { useNavigate } from 'react-router-dom';
import { testimonialHeaderPart1, testimonialHeaderPart2 } from '../../urls/contactusKeys';


function Testimonial({margins,paddings}) {
    const navigate=useNavigate()


    return (
        <>
            <Box >
                <Grid id='testimonialHeader' container spacing={2} mt={margins} pt={paddings} style={{ backgroundColor: '#051d33' }}>

                    {/* <Grid item xs={12} md={2}>
                            </Grid> */}
                    <Grid item xs={12} md={12} align="center" id="testimonials"
                    // style={{ padding: '60px' }}
                    >
                        <Box id="cardHeight" style={{ backgroundColor: `#051d33`, height: 'auto', borderRadius: '30px', textAlign: 'center'}}>
                            <Grid container spacing={2}>
                                <Grid align="center" item xs={12} md={12} style={{ display: 'flex', flexDirection: 'column', paddingBottom: '5%', paddingTop: '10%', justifyContent: 'center', alignItems: 'center' }}>

                                    <Typography className='poppinsSemiRegularBold' id="sectionTestimonial" variant='h2' style={{ width: '100%' }}>
                                     {testimonialHeaderPart1}  <br />{testimonialHeaderPart2}
                                    </Typography>
                                    <span style={{marginTop:'3%'}}>
                                    <ContainedButton text="Buy Programs" onClick={()=>navigate('/products')} />

                                    </span>


                                </Grid>
                                {/* <Grid item xs={12} md={12}  style={{ display: 'flex', flexDirection: 'column', paddingBottom: '5%', justifyContent: 'center', alignItems: 'center' }}>
                                    <Avatar src={arrow} alt="arrow" variant='square' style={{
                                        width: '50%', height: 'auto',opacity:0.5
                                        // display: { xs: 'none', md: 'block' }
                                    }} /></Grid> */}

                            </Grid>


                        </Box>
                    </Grid>
                </Grid>
            </Box>
        </>
    );
}

export default Testimonial;