import { Avatar, Grid, Typography, Button, List, ListItemIcon, ListItem, ListItemButton, ListItemText, Stack } from '@mui/material';
import * as React from 'react';
import "../../App.css"
import logo from "../../assets/dark_logo.png"
import CallIcon from '@mui/icons-material/Call';
import LocationOnIcon from '@mui/icons-material/LocationOn';
// import './stylesheet.css'
import EmailOutlinedIcon from '@mui/icons-material/EmailOutlined';
import { useNavigate } from 'react-router-dom';
import { FooterContent, emailKey, locationKey, phoneNoKey } from '../../urls/contactusKeys';
import { globalColor } from '../../utils/globalColor';
function Footer() {
    const navigate = useNavigate();
    return (
        <>

            <Grid container spacing={2} style={{ marginTop:-4,backgroundColor: globalColor, height: 'auto', paddingInline: '6%', paddingBlock: '1%' }}>
                <Grid item xs={12} md={1}></Grid>
                <Grid item xs={12} md={3} align="left">
                    {/* <div style={{backgroundColor:'white',width:'50%',borderRadius:'10px'}}> */}
                    <Avatar sx={{ width: "150px", height: 'auto', paddingBlock: '4%' }} alt="Logo" src={logo} variant='square' />

                    {/* </div> */}
                    <Typography variant='h6' className='poppinsRegularBold' style={{ fontSize: '18px', color: 'white', fontweight: 100 }}>
                        {FooterContent}
                    </Typography>
                    <Stack
                        sx={{
                            width: '100%',
                            marginBlock: '5%'
                        }}

                        direction={'row'}
                    >
                        <a
                            target='_blank'
                            href="https://facebook.com/zipto.io">
                            <img src="https://res.cloudinary.com/de2dei2qe/image/upload/v1698998562/oodsval7hgqbplb8xclf.png"
                                style={{ width: '25px', height: '25px', marginRight: '15px' }}
                            />
                        </a>
                        <a
                            target='_blank'
                            href="https://instagram.com/zipto.io">
                            <img src="https://res.cloudinary.com/de2dei2qe/image/upload/v1698998562/brja7bvvoxqzhp8e4ukd.png"
                                style={{ width: '25px', height: '25px', marginRight: '15px' }}
                            />
                        </a>
                        <a
                            target='_blank'
                            href="https://www.linkedin.com/company/74069893">
                            <img src="https://res.cloudinary.com/de2dei2qe/image/upload/v1698998562/d50w667rsooidk2jgtx0.png"
                                style={{ width: '25px', height: '25px', marginRight: '15px' }}
                            />
                        </a>
                    </Stack>
                </Grid>
                <Grid item xs={12} md={3}
                    sx={{
                        // backgroundColor:'red',
                        display: 'flex',
                        flexDirection: {
                            xs: 'column',
                            md: 'row'
                        },
                    }}
                    id="footernav" >


                    {/* <List> */}
                    <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'left', padding: 0 }}>
                        

                    </div>

                   
                </Grid>
                <Grid item xs={12} md={5}
                    sx={{
                        // backgroundColor:'red',
                        display: 'flex',
                        flexDirection: {
                            xs: 'column',
                            md: 'row'
                        },
                    }}
                    id="footernav">
                    <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'left', padding: 0 }}>
                        <Typography className='poppinsSemiRegularBold' id="headerFooter" variant='h5' style={{ color: 'white', marginBottom: '10px' }}>
                            Contact us
                        </Typography>
                        <span
                            onClick={() => window.open('tel:' + phoneNoKey)}
                            className='poppinsRegularBold' style={{
                                padding: '5px',
                                display: 'flex',
                                // backgroundColor:'red',
                                borderRadius: 8,
                                color: 'white',
                                cursor: 'pointer',
                            }}>
                            <CallIcon style={{ marginRight: '4px' }} />
                            {phoneNoKey}

                        </span>
                        <span
                            onClick={() => {
                                window.open('mailto:' + emailKey)
                            }}
                            className='poppinsRegularBold' style={{
                                padding: '5px',
                                display: 'flex',
                                // backgroundColor:'red',
                                borderRadius: 8,
                                color: 'white',
                                cursor: 'pointer',
                            }}>
                            <EmailOutlinedIcon style={{ marginRight: '4px' }} />
                            {emailKey}


                        </span>
                        <span className='poppinsRegularBold' style={{
                            padding: '5px',
                            display: 'flex',
                            // backgroundColor:'red',
                            borderRadius: 8,
                            color: 'white',
                            cursor: 'pointer',
                        }}>
                            <LocationOnIcon style={{ marginRight: '4px' }} />
                            {locationKey}



                        </span>
                    </div>
                    {/* <List>
                       
                        <ListItem key='call' disablePadding>
                            <ListItemIcon>
                                <CallIcon />
                            </ListItemIcon>
                            <ListItemText id='sectionFooterList' primary="+1(970)394-1523" />
                        </ListItem>

                        <ListItem key='email' disablePadding>
                            <ListItemIcon>
                                <EmailOutlinedIcon />
                            </ListItemIcon>
                            <ListItemText id='sectionFooterList' primary="info@mtechub.com" />
                        </ListItem>


                        <ListItem key='location_1' disablePadding>
                            <ListItemIcon>
                                <LocationOnIcon />
                            </ListItemIcon>
                            <ListItemText id='sectionFooterList' primary="Office # 11 maryam business center ISB ,PAK" />
                        </ListItem>

                    </List> */}
                </Grid>
            </Grid>


        </>
    );
}

export default Footer;