import { Button, Grid, Typography } from '@mui/material';
import * as React from 'react';
import "../../App.css"
import { globalColor } from '../../utils/globalColor';

function OutlinedButton({text}) {
    return (
        <>

            <Button
                className='poppinsRegularBold'
                sx={{
                    width: '100%',
                    // display: { xs: 'none', md: 'block' },
                    color: globalColor,
                    fontSize: '16px',
                    borderRadius: '50px',
                    border:'1px solid #03356b',
                    marginLeft: '10px',

                    textTransform: 'capitalize',
                  transition: 'background-color 0.3s, color 0.3s', // Add transition properties

                    '&:hover': {
                        bgcolor: "#03356b",
                        color: 'white', 
                    transitionDelay: '0.3s', // Add transition delay for hover

                    },

                }} >{text}</Button>
        </>
    );
}

export default OutlinedButton;